
.skillsbox {
  display: grid;
  border: 0px solid white;
}


@media screen and (width <= 290px) {
  .skillsbox {
    display: grid;
     grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (290px<=width <= 320px) {
  .skillsbox {
    display: grid;
     grid-template-columns: repeat(3, 1fr);
  }
}
 @media screen and (320px< width <= 768px) {
  .skillsbox {
    display: grid;
     grid-template-columns: repeat(3, 1fr);
  }
} 

 @media screen and (768px< width <= 960px) {
  .skillsbox {
    display: grid;
     grid-template-columns: repeat(3, 1fr);
  }
} 
@media screen and (960px < width <= 1200px) {
  .skillsbox {
    display: grid;
     grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (1200px < width <= 1536px) {
  .skillsbox {
    display: grid;
     grid-template-columns: repeat(4, 1fr);
  }
}

