.App {
  text-align: center;
  
 
  
}

body{
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


/* width */
::-webkit-scrollbar{
  width: 10px;

}
/* Track */
::-webkit-scrollbar-track:vertical {
  background: #a11414;
  box-shadow: inset 0 0 3px rgb(161, 114, 192);
  border-radius: 10px;
  background-color: #13022C;
}

/* Handle */
::-webkit-scrollbar-thumb:vertical {
  width: 1px;
  background: rgb(205, 199, 208);
  border-radius: 20px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:vertical:hover {
  background: rgb(157, 140, 168);
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


