.craousel {
  /* display: none; */
  border: 0px solid white;
}
@media screen and (width <= 280px) {
  .craousel {
    width: 190px;
  }
}

/* ['270px','400px','600px','750px','500px'] */
@media screen and (280px<width <= 320px) {
  .craousel {
    width: 270px;
  }
}
@media screen and (320px< width <= 413px) {
  .craousel {
    width: 280px;
  }
}
@media screen and (413px< width <= 520px) {
  .craousel {
    width: 310px;
  }
}
@media screen and (520px< width < 768px) {
  .craousel {
    width: 480px;
  }
}

@media screen and (768px<= width <= 960px) {
  .craousel {
    width: 630px;
  }
}
@media screen and (960px < width <= 1200px) {
  .craousel {
    width: 750px;
  }
}

@media screen and (1200px < width <= 1536px) {
  .craousel {
    width: 500px;
  }
}
